@import "sass/index";
@import "./variables.scss";
@import "../../variables.module.scss";
.root {
  position: relative;
  padding: 5px 10px;
  background: $default;
  @include backgroundOptions;
  display: flex;
  flex-direction: column;
  height: #{$rootWidth}px;
  @media (max-width: 600px) {
    padding: 5px;
    height: auto;
    overflow: visible;
    & > div {
      & > div:last-child {
        transform: scale(1);
        height: auto;
        & > div:first-child {
          transform: translateY(0);
        }
      }
    }
  }
}

.deck {
  position: absolute;
  display: flex;
  align-self: flex-end;
  top: 27%;
  width: 58.45px;
  height: 92.06px;

  & > img {
    width: auto;
    height: 100%;
  }

  @media (max-width: 600px) {
    & > img {
      width: auto;
      height: 80px;
    }
  }
}
