@import "../../variables.scss";
.root {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transform-origin: top center;
  gap: 5px;

  height: 100%;
  @media (max-width: 600px) {
    padding: 5px;
    gap: 5px;
    height: 240px;
    transform: scale(0.5);
  }
}

.userBox {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  & > div {
    @media (max-width: 500px) {
      transform: translateY(0px) !important;
    }
  }
}
.userHandRoot {
}

.wrapper {
  height: 100%;
  max-height: #{$rootWidth}px;

  @media (max-width: 600px) {
    height: fit-content;
    max-height: 100%;
  }
}
.labelsWrapper.labelsWrapper {
  font-weight: 500;
  font-size: 6.81919px;
  line-height: 8px;
  span {
    height: 15px;
    width: 103px;
    justify-content: center;
    text-align: center;

    &::before {
      border: 8px solid #4f5768;
      width: 19px;
      top: 1px;
      left: -14px;
      border-left: 11px solid transparent;
      transform: translateZ(-1px);
    }
    &:after {
      border: 8px solid #4f5768;
      width: 19px;
      top: 1px;
      right: -14px;
      border-right: 11px solid transparent;
      transform: translateZ(-1px);
    }
  }
}
.cardRoot.cardRoot {
  width: 40px;
  height: 95px;
  transition: all 0s;
  transform: rotateY(0deg) translateX(-15%);
  animation: none !important;
}
.cardSize.cardSize {
  width: 58px;
  height: 82px;
  & > div:first-child {
    transform: scale(0.5) translate(-24px, -28px);
  }
  & > div:nth-child(2) {
    transform: scale(0.5) translate(-7px, -97px);
  }
  & > div:last-child {
    transform: scale(0.5) translate(24px, -152px);
  }
}
.hand.hand {
  width: 100% !important;
  height: auto !important;
  justify-content: center;
}
.score.score {
  padding: 1px 11px;

  font-weight: 700;
  font-size: 10.7614px;
  line-height: 11px;
  @media (max-width: 500px) {
    transform: translateY(-5px);
  }
}
